import React from 'react'
import Modal from 'react-modal';

function HelpUserModal({ isOpen, onClose, title }) {


    const customStyle = {
        content: {
            top: "50%",
            left: "50%",
            right: "auto",
            bottom: "auto",
            marginRight: "-50%",
            transform: "translate(-50%, -50%)",
            maxWidth: "60%",
            padding: "20px",
        },
    };
    const btnStyle = {

        width: '7rem',
        fontSize: '1rem',
        padding: '.2rem',
        backgroundColor: '#3584E4',
        color: '#fff',
        borderRadius: '5px'

    }



    return (
        <Modal
            isOpen={isOpen}
            onRequestClose={onClose}
            style={customStyle}
            title={title}
        >
            <div className="container">
                <h1>{title}</h1>
                <p>
                    Este formulrio permite gerenciar os registros dos usurios de
                    forma simples e eficiente. Aqui esto os recursos principais:<br /><br />

                    <b>Cadastro de Usurios:</b><br />
                    Utilize o boto "Cadastrar" para adicionar um novo usurio ao sistema.
                    Preencha os campos obrigatrios, como nome, idade, sexo e informaes
                    pessoais, e clique em "Salvar".<br /><br />

                    <b>Atualizao de Dados:</b><br />
                    Para atualizar as informaes de um usurio existente, clique no boto
                    "Editar". Isso abrir um formulrio preenchido com os detalhes atuais
                    do usurio, onde voc pode fazer as alteraes necessrias e, em seguida,
                    clique em "Salvar Alteraes".<br /><br />

                    <b>Excluso de Usurios:</b><br />
                    Se necessrio, remova um usurio do sistema utilizando o boto "Deletar".
                    Lembre-se: esta ao  irreversvel e exclui permanentemente os dados do
                    usurio do nosso registro.<br /><br />

                    <b>Pesquisa por Nome:</b><br />
                    Utilize a barra de pesquisa para encontrar um usurio especfico pelo nome.
                    Digite o nome no campo de pesquisa e pressione "Enter" para visualizar os
                    resultados correspondentes.<br /><br />

                    <b>Impresso de Dados:</b><br />
                    Caso necessite de um registro fsico, use o boto "Imprimir" para obter
                    uma verso impressa dos dados dos usurios.<br /><br />

                    Este formulrio foi projetado para facilitar o gerenciamento dos registros
                    dos nossos usurios. Se precisar de mais ajuda ou tiver dvidas
                    especficas sobre o uso deste formulrio, no hesite em nos contatar.<br />

                    Obrigado por utilizar nosso sistema de cadastro de usurios!
                </p>
                <button style={btnStyle} onClick={onClose}>Fechar</button>
            </div>
        </Modal>
    )
}

export default HelpUserModal