export const AjudaVisitantes = 
    <p>Nessa pgina voc ter acesso a lista de todos os visitantes cadastrados!<br/><br />
    A cima da tabela possui um filtro onde voc pode pesquisar pelo nome e/ou sobrenome do visitante.<br/><br />
    As aes incluem a edio (boto com lpis), na qual pode se editar os dados do visitante, e excluso (boto com lixeira), na qual ir excluir o visitante.<br/><br />
    No final da pgina possui o boto Relatrio que permiti imprimir ou salvar como pdf a lista de visitantes cadastrados.<br/><br />
    E por fim o boto Cadastrar que ir direcionar o usurio ao formulrio de cadastro de um novo visitante.</p>


